import React from 'react';
import classes from './Card.module.css';


const Card=(props)=>{

	return (
			
			<div onClick={props.selectPhoto} className={classes.Card}>

			<img src={props.thumbnailURL} className="gallery-image" alt="" />

		<div className={classes.Info}>

			<p>{props.title} </p>

		</div>

</div>
			


			)
}

export default Card;

